import React from 'react';
import { Box, Image, Layer, Spinner } from 'grommet';
import Countdown from "react-countdown";
import Result from "assets/result.png";

interface LoadingModalType {
  openModal: boolean;
  setOpenModal: React.Dispatch<React.SetStateAction<boolean>>;
  isCompleted: boolean;
  setIsCompleted: React.Dispatch<React.SetStateAction<boolean>>;
};

const LoadingModal = ({ openModal, setOpenModal, isCompleted, setIsCompleted }: LoadingModalType) => {

  return (
    <div>
      {openModal && (
        <Layer>
          <Box
            align="center"
            justify="center"
            gap="small"
            pad="large"
          >
            <Spinner
              animation={{ type: 'pulse', duration: 650, size: 'medium' }}
              justify="center"
            >
            </Spinner>
            <br/>
            <Countdown
              date={Date.now() + 5000}
              intervalDelay={0}
              precision={3}
              renderer={props => <div>{props.seconds}.{props.milliseconds}</div>}
            />
          </Box>
        </Layer>
      )}
      {isCompleted && (
        <Layer position="center" onClickOutside={() => setIsCompleted(false)} onEsc={() => setIsCompleted(false)}>
          <Box
            align="center"
            justify="center"
            direction="row"
            alignSelf="center"
            pad="medium"
          >
            <Image src={Result} />
          </Box>
      </Layer>
      )}
    </div>
  );
};

LoadingModal.parameters = {
  chromatic: { disable: true },
};

export default LoadingModal;